import React from 'react';
import { NavLink } from 'react-router-dom';
import web from "../src/images/rw5.gif";
import "./Home.css";
 

const Home=()=>{
    return(
        <>
            <section id= "header" className="d-flex align-items-center">
            <div className="container-fluid ">
                <div className="row">
                    <div className="col-10 mx-auto">
                    <div className="row">
                    <div className=" content col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                        <h1>A simple website using <strong >React</strong></h1>
                        <h2 className="my-3">As the task of internship at <strong className="brand-name">Tathastu</strong></h2>
                        <div className="mt-3">
                            <NavLink to="/about" className="btn btn-outline-primary">Get Started</NavLink>
                        </div>
                    </div>

                    <div className="col-lg-5 order-1 order-lg-2 header-img">
                        <img src={web } alt="image"/>

                    </div>
                    </div>

                    </div>
                </div>
            </div>

            </section>
        </>

    )

}
export default Home; 